import React, { useEffect, useState } from 'react';
import '../App.css';
import { useParams, useHistory } from "react-router-dom";

function DetailedTown() {

  const { lon, lat, name } = useParams();
  const [city, setCity] = useState({});

  const api = {
    base: "https://api.openweathermap.org/data/2.5/onecall?",
    search: "&exclude=minutely&lang=hr&units=metric",
    key: "26f7c14de162ddf380af26c56863bd3a"
  }

  useEffect(() => {
    fetch(`${api.base}lat=${lat}&lon=${lon}&appid=${api.key}${api.search}`)
      .then(response => response.json())
      .then(result => {
        setCity(result)
      })
  }, [api.base, api.key, api.search, lat, lon])

  let history = useHistory();

  const returnHome = () => {
    history.push('/');
  }

  let future = null;

  if (city.hourly) {
    future = city.hourly.slice(1, 10).map((hour) => {
      const date = new Date(hour.dt * 1000);
      return (
        <div className="detailsBox" key={hour.dt}>
          <img src={`http://openweathermap.org/img/wn/${hour.weather[0].icon}@2x.png`} alt={name} />
          <div className="detailsBoxInfo">
            <div className="detailsWeather">{hour.weather[0].description}</div>
            <div className="detailsTemp">{Math.round(hour.temp)} °C</div>
            <div className="detailsRealFeel">
              Dojam: {Math.round(hour.feels_like)} °C<br></br>
              Vjetar: {Math.round(hour.wind_speed * 3.6)} km/h<br></br>
              {date.getHours()}:00, {date.getDay() + 1}.{date.getMonth() + 1}.{date.getFullYear()}.
            </div>
          </div>
        </div>
      )
    })
  }

  return (
    <>
      {
        city.current ?
          < div className="weatherResults" >
            <div className="locationBox">
              {name}
            </div>
            <div className="weatherBox">
              <img src={`http://openweathermap.org/img/wn/${city.current.weather[0].icon}@2x.png`} alt={name} />
              <div className="weatherBoxInfo">
                <div className="weather">{city.current.weather[0].description}</div>
                <div className="temp">{Math.round(city.current.temp)} °C</div>
                <div className="realFeel">
                  Dojam: {Math.round(city.current.feels_like)} °C<br></br>
                  Vjetar: {Math.round(city.current.wind_speed * 3.6)} km/h
                </div>
              </div>
            </div>
            {future}
            <div className="options">
              <button className="returnBtn" onClick={returnHome}>Natrag</button>
            </div>
          </div >
          :
          <h1>Loading...</h1>
      }
    </>
  )
}

export default DetailedTown;